html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  background-color: #c2bebe;
  min-width: 325px;
}
@media (min-width: 768px) {
  body {
    padding: 30px 0;
    background: url("/img/background.jpg") no-repeat;
    background-size: cover;
    background-attachment: fixed;
  }
}

.center {
  max-width: 1280px;
  padding: 0;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .center--mobile {
    max-width: 340px;
  }
}
@media (min-width: 768px) {
  .center--flex {
    padding: 0 10px;
    display: flex;
    column-gap: 50px;
    position: relative;
    justify-content: center;
  }
}
@media (min-width: 1220px) {
  .center--flex {
    justify-content: flex-end;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button__link {
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  position: relative;
}
.button__link:hover {
  text-decoration: underline;
}
.button__link::before {
  position: absolute;
  content: "";
  width: 5px;
  height: 5px;
  border-top: 2px solid #1d1b1b;
  border-right: 2px solid #1d1b1b;
  transform: rotate(45deg);
  top: 7px;
  right: -15px;
  transition: right 0.3s;
}
.button__link:hover::before {
  right: -25px;
}
.button__link::after {
  position: absolute;
  content: "";
  width: 5px;
  height: 5px;
  border-top: 2px solid #1d1b1b;
  border-right: 2px solid #1d1b1b;
  transform: rotate(45deg);
  top: 7px;
  right: -20px;
  transition: right 0.3s;
}
.button__link:hover::after {
  right: -30px;
}
.button__link--disabled {
  cursor: default;
  opacity: 0.5;
}
.button__link--disabled:hover {
  text-decoration: none;
}
.button__link--disabled:hover::before {
  right: -15px;
}
.button__link--disabled:hover::after {
  right: -20px;
}

.contacts {
  list-style: none;
}

.contacts__item {
  margin-bottom: 10px;
}
.contacts__item:last-child {
  margin-bottom: 0;
}

.contacts__link {
  color: inherit;
  text-decoration: none;
  position: relative;
}
.contacts__link:hover {
  text-decoration: underline;
}
.contacts__link--phone::before {
  position: absolute;
  content: "";
  width: 27px;
  height: 27px;
  background: url("/img/icons/phone.svg") no-repeat;
  top: 0;
  left: -33px;
}
.contacts__link--email::before {
  position: absolute;
  content: "";
  width: 27px;
  height: 17px;
  background: url("/img/icons/email.svg") no-repeat;
  top: 3px;
  left: -35px;
}
.contacts__link--telegram::before {
  position: absolute;
  content: "";
  width: 27px;
  height: 20px;
  background: url("/img/icons/telegram.svg") no-repeat;
  top: 3px;
  left: -35px;
}

.education {
  list-style: none;
}

.education__item {
  margin-bottom: 15px;
  position: relative;
}
.education__item:last-child {
  margin-bottom: 0;
}
.education__item::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  background: url("/img/icons/hat.svg") no-repeat;
  top: 3px;
  left: -35px;
}

.education__text {
  padding: 0;
  margin: 0;
}

.info {
  background-color: #f7f0e9;
  line-height: 24px;
  margin-bottom: 1px;
}
@media (min-width: 768px) {
  .info {
    border-radius: 10px;
    box-shadow: 0 0 10px 3px #4e4c4c;
    max-width: 35%;
    align-self: start;
    position: sticky;
    left: 0;
    top: 30px;
    overflow: hidden;
  }
}
@media (min-width: 1220px) {
  .info {
    max-width: 40%;
  }
}

.info__name {
  background-color: #1d1b1b;
  color: #f7f0e9;
  font-size: 35px;
  line-height: 35px;
  margin: 0;
  padding: 15px;
}
@media (min-width: 1220px) {
  .info__name {
    padding: 20px;
    font-size: 40px;
    line-height: 40px;
  }
}

.info__note {
  font-size: 20px;
  padding: 0;
  margin: 0;
}

.info__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .info__list {
    overflow-y: scroll;
    max-height: 70vh;
  }
  .info__list::-webkit-scrollbar {
    width: 0;
  }
}
@media (min-width: 1220px) {
  .info__list {
    max-height: none;
  }
}

.info__item {
  overflow: hidden;
}

.info__text {
  display: block;
  border-top: 1px solid #c2bebe;
  padding: 5px 20px;
  position: relative;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #f7f0e9;
  font-weight: 700;
}
.info__text:hover {
  background-color: #1d1b1b;
  color: #f7f0e9;
}
.info__text::before {
  position: absolute;
  content: "";
  width: 7px;
  height: 7px;
  border-top: 1px solid #1d1b1b;
  border-right: 1px solid #1d1b1b;
  top: 12px;
  transform: rotate(135deg);
  right: 30px;
  transition: transform 0.5s, top 0.5s;
}
.info__text--opend::before {
  transform: rotate(-45deg);
  top: 15px;
}
.info__text:hover::before {
  border-top: 1px solid #f7f0e9;
  border-right: 1px solid #f7f0e9;
}

.info__item:first-child .info__text:hover {
  border-top: 1px solid #4e4c4c;
}

@media (min-width: 768px) {
  .info__item:last-child .info__text {
    border-bottom: none;
  }
}

.info__inner {
  margin: 0;
  padding: 5px 10px 10px 45px;
  max-height: 1000px;
  z-index: -1;
  transition: all 0.5s;
  font-size: 15px;
  line-height: 20px;
}
.info__inner--hidden {
  max-height: 0;
  padding: 0 10px 0 45px;
  transition: all 0.3s;
}
@media (min-width: 1220px) {
  .info__inner {
    padding: 5px 10px 10px 55px;
    font-size: inherit;
    line-height: inherit;
  }
  .info__inner--hidden {
    max-height: 0;
    padding: 0 10px 0 55px;
    transition: all 0.3s;
  }
}

.info__item:first-child .info__inner {
  padding: 5px 20px 10px;
  text-indent: 10px;
  line-height: 20px;
}
@media (min-width: 1220px) {
  .info__item:first-child .info__inner {
    padding: 5px 30px 10px;
    text-indent: 20px;
  }
}
.info__item:first-child .info__inner--hidden {
  max-height: 0;
  padding: 0 20px;
  transition: all 0.3s;
}
@media (min-width: 1220px) {
  .info__item:first-child .info__inner--hidden {
    padding: 0 30px;
  }
}

.main {
  padding: 30px 10px 20px 20px;
  background-color: #f7f0e9;
}
@media (min-width: 768px) {
  .main {
    max-width: 65%;
    box-shadow: 0 0 10px 3px #4e4c4c;
    padding: 30px 40px 20px 40px;
  }
}
@media (min-width: 1220px) {
  .main {
    max-width: 60%;
    padding: 40px 60px 20px 60px;
  }
}

.main__heading {
  text-transform: uppercase;
  margin: 0 0 30px 0;
  padding: 0;
  font-size: 24px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .main__heading {
    font-size: 28px;
    line-height: 30px;
  }
}
@media (min-width: 1220px) {
  .main__heading {
    margin: 0 0 40px 0;
    font-size: 30px;
    line-height: 30px;
  }
}

.portfolio {
  display: grid;
  grid-template-columns: 1fr;
  gap: 60px;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 1220px) {
  .portfolio {
    grid-template-columns: 1fr 1fr;
  }
}

.portfolio__item {
  display: flex;
  flex-direction: column;
  max-width: 300px;
}

.portfolio__item-heading {
  order: 2;
  margin: 10px 0 0 0;
}

.portfolio__img {
  outline: 3px double #c2bebe;
}

.portfolio_description {
  order: 3;
}

.portfolio__buttons {
  list-style: none;
  margin: 0;
  padding: 0;
  order: 3;
}

.portfolio__buttons-item {
  margin-bottom: 5px;
}
.portfolio__buttons-item:last-child {
  margin-bottom: 0;
}

.skills {
  list-style: none;
}
@media (min-width: 1220px) {
  .skills {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
  }
}

.skills__item {
  position: relative;
}
.skills__item::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  background: url("/img/icons/mark.svg") no-repeat;
  top: 4px;
  left: -30px;
}

.scroll-up {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  box-shadow: 0 -2px 6px #4e4c4c;
  background-color: #f7f0e9;
  opacity: 0;
  transition: background-color 0.5s, opacity 1s;
  text-align: center;
  text-decoration: none;
  color: inherit;
  padding: 5px 0;
}
@media (min-width: 768px) {
  .scroll-up {
    width: 42px;
    height: 42px;
    top: auto;
    bottom: 20px;
    right: 20px;
    border-radius: 30px;
    box-shadow: 0 2px 6px #4e4c4c;
    padding: 0;
  }
  .scroll-up::before {
    position: absolute;
    content: "";
    background-color: #1d1b1b;
    border-radius: 5px;
    transform: rotate(-50deg);
    transition: background-color 0.5s;
    width: 23px;
    height: 7px;
    top: 17px;
    left: 5px;
  }
  .scroll-up::after {
    position: absolute;
    content: "";
    background-color: #1d1b1b;
    border-radius: 5px;
    transform: rotate(50deg);
    transition: background-color 0.5s;
    width: 23px;
    height: 7px;
    top: 17px;
    left: 16px;
  }
}
@media (min-width: 768px) and (min-width: 1220px) {
  .scroll-up {
    width: 52px;
    height: 52px;
  }
  .scroll-up::before {
    width: 25px;
    height: 8px;
    top: 21px;
    left: 8px;
  }
  .scroll-up::after {
    width: 25px;
    height: 8px;
    top: 21px;
    left: 20px;
  }
}
.scroll-up:hover {
  background-color: #1d1b1b;
}
.scroll-up:hover::before, .scroll-up:hover::after {
  background-color: #f7f0e9;
}
.scroll-up--show {
  display: block;
  opacity: 1;
}

.scroll-up__text {
  text-transform: uppercase;
  font-weight: 700;
  padding: 0;
}
@media (min-width: 768px) {
  .scroll-up__text {
    display: none;
  }
}